.dvs-loadbar {
  height:8px;
  @apply dvs-bg-grey-lighter dvs-rounded dvs-w-3/5 dvs-relative;

  .dvs-bar {
    height: 6px;
    top:1px;
    left:1px;
    right:1px;
    bottom:1px;
    @apply dvs-absolute dvs-bg-blue-dark dvs-rounded;

    transition: 0.3s width ease;
  }
}



