.fade-enter-active {
  transition: opacity .5s;
  transition-delay: .25s;
}

.fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}
.fade-enter-to {
  opacity: 1
}



