.dvs-btn {
  @apply dvs-p-4 dvs-bg-blue-dark dvs-rounded dvs-text-white dvs-uppercase dvs-text-sm;
  transition: background-color 0.5s;
}

.dvs-btn:focus {
  outline-style: none;
}

.dvs-btn.dvs-toggle {
