}

.dvs-btn.dvs-btn-plain {
  @apply dvs-bg-grey;
}

.dvs-btn.dvs-btn-plain:hover {
  @apply dvs-bg-blue-dark;
}

.dvs-btn.dvs-btn-ghost {
  @apply dvs-bg-transparent dvs-border-2 dvs-border-blue-dark dvs-text-blue-dark;
}

.dvs-btn.dvs-btn-ghost:hover {
  @apply dvs-bg-blue-dark dvs-text-white;
}

.dvs-btn.dvs-btn-sm {
  @apply dvs-py-3 dvs-px-4;
}

.dvs-btn.dvs-btn-xs {
  @apply dvs-py-2 dvs-px-3 dvs-text-xs;
}

.dvs-btn.dvs-btn-lg {
  @apply dvs-py-6 dvs-px-4 dvs-text-lg;
}

.dvs-btn:disabled {
  @apply dvs-bg-grey-light;
