.dvs-card {
  @apply dvs-p-8 dvs-shadow dvs-rounded-sm;
}

.vc-photoshop {
  font-family: system-ui, BlinkMacSystemFont, -apple-system, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif !important;
}

.vc-ps-fields .vc-input__input {
  @apply dvs-px-2 dvs-py-1;
}

#devise-container {
  @apply dvs-font-sans dvs-w-full dvs-flex dvs-flex-row-reverse dvs-overflow-scroll;
}

#devise-admin-open {
  @apply dvs-fixed dvs-pin-b dvs-pin-l dvs-opacity-25 dvs-cursor-pointer dvs-text-3xl  dvs-ml-4 dvs-mb-4 dvs-z-50;
  transition: all 0.5s;
}

#devise-admin-open:hover {
  @apply dvs-opacity-100;
  margin-left: 1.1rem;
  margin-bottom: 1.1rem;
}

#devise-admin {
  @apply dvs-z-30 dvs-fixed dvs-pin-l dvs-pin-t dvs-pin-b dvs-overflow-scroll dvs--ml-px;
  width: 350px;
  transition: left 0.5s ease-out;
}

#devise-admin-shim {
  @apply dvs-border-r dvs-border-grey-light dvs-bg-white;
  min-width: 350px;
  width: 350px;
  transition: width 0.25s ease-out, min-width 0.25s ease-out;
}

.devise-content {
  @apply dvs-z-10;
  transition: width 0.25s ease-out, height 0.25s ease-out, margin 0.25s ease-out;
  width: 100%;
}

.devise-content.tablet {
  @apply dvs-shadow-lg dvs-my-8 dvs-rounded;
  width: 768px;
  height: 1024px;
}

.devise-content.mobile-landscape {
  @apply dvs-shadow-lg dvs-my-12 dvs-rounded;
  width: 600px;
  height: 320px;
}

.devise-content.mobile-portrait {
  @apply dvs-shadow-lg dvs-my-10 dvs-rounded;
  width: 320px;
  height: 600px;
}

#devise-container.admin-closed #devise-admin {
  left: -500px;
}

#devise-container.admin-closed #devise-admin-shim {
  @apply dvs-min-w-0;
  width: 0px;
}

#devise-container.admin-closed #devise-content {
  width: 100%;
}

#devise-container.admin-closed #devise-content.tablet {
  @apply dvs-shadow-lg dvs-my-8 dvs-rounded;
  width: 768px;
  height: 1024px;
}

#devise-container.admin-closed #devise-content.mobile-landscape {
  @apply dvs-shadow-lg dvs-my-12 dvs-rounded;
  width: 600px;
  height: 320px;
}

#devise-container.admin-closed #devise-content.mobile-portrait {
  @apply dvs-shadow-lg dvs-my-10 dvs-rounded;
  width: 320px;
  height: 600px;
}

#devise-container #devise-user > div {
  width: 350px;
  height: 99px;
}

#devise-container #devise-user > div :first-child {
  @apply dvs-w-3/4;
}

#devise-container #devise-user > div :nth-child(2) {
  @apply dvs-w-1/4 dvs-justify-center;
}

#devise-container.preview-frame .devise-content {
  width: 100vw;
}

#devise-container.wide-admin #devise-admin {
  @apply dvs-w-full;
}

#devise-container.wide-admin #devise-admin-shim {
  @apply dvs-w-full;
}

#devise-container.wide-admin #devise-content {
  display: none;
}

#devise-sidebar {
  @apply dvs-border-r dvs-border-grey-light dvs-bg-white dvs-p-8 dvs-fixed dvs-pin-l dvs-pin-b dvs-pin-t dvs-overflow-y-scroll dvs-z-20;
  top: 100px;
  min-width: 350px;
  width: 350px;
}

#devise-sidebar.devise-iframe-sidebar {
  top: 0;
}

#devise-admin-content {
  @apply dvs-absolute dvs-pin-t dvs-pin-r dvs-pin-b dvs-z-10 dvs-p-8 dvs-overflow-scroll;
  width: calc(100% - 350px);
}

#devise-preview-content {
  @apply dvs-z-10 dvs-absolute dvs-pin-t dvs-pin-r dvs-pin-b;
  width: calc(100% - 350px);
}

#devise-preview-iframe {
  height: calc(100vh - 100px);
}

.dz-image,
.dz-error-mark,
.dz-success-mark {
  @apply dvs-hidden;
}

.dvs-fieldset label {
  @apply dvs-block dvs-mb-2 dvs-text-xs dvs-uppercase dvs-font-bold dvs-tracking-superwide;
}

.dvs-fieldset label.dvs-large-label {
  @apply dvs-text-base dvs-font-normal dvs-normal-case dvs-tracking-normal;
}

.dvs-fieldset label.dvs-small-label {
  @apply dvs-text-sm dvs-font-normal dvs-normal-case dvs-tracking-normal;
}

.dvs-fieldset textarea,
.dvs-fieldset select,
.dvs-fieldset input[type="text"],
.dvs-fieldset input[type="password"],
.dvs-fieldset input[type="date"],
.dvs-fieldset input[type="datetime-local"],
.dvs-fieldset input[type="number"],
.dvs-fieldset input[type="email"],
.dvs-fieldset input[type="phone"] {
  @apply dvs-p-4 dvs-w-full dvs-bg-grey-lighter dvs-rounded-sm dvs-appearance-none;
}

.dvs-fieldset textarea.dvs-w-auto,
.dvs-fieldset select.dvs-w-auto,
.dvs-fieldset input[type="text"].dvs-w-auto,
.dvs-fieldset input[type="password"].dvs-w-auto,
.dvs-fieldset input[type="date"].dvs-w-auto,
.dvs-fieldset input[type="datetime-local"].dvs-w-auto,
.dvs-fieldset input[type="number"].dvs-w-auto,
.dvs-fieldset input[type="email"].dvs-w-auto,
.dvs-fieldset input[type="phone"].dvs-w-auto {
  @apply dvs-w-auto;
}

.dvs-toggle {
  @apply dvs-inline-block dvs-relative;
  height: 34px;
  width: 60px;
}

.dvs-toggle input {
  @apply dvs-hidden;
}

.dvs-toggle input:checked + .dvs-toggle-slider {
  background-color: #66bb6a;
}

.dvs-toggle input:checked + .dvs-toggle-slider:before {
  transform: translateX(26px);
}

.dvs-toggle .dvs-toggle-slider {
  @apply dvs-cursor-pointer dvs-absolute dvs-pin;
  background-color: #ccc;
  transition: .4s;
  border-radius: 34px;
}

.dvs-toggle .dvs-toggle-slider:before {
  @apply dvs-absolute;
  content: "";
  background-color: #fff;
  bottom: 4px;
  left: 4px;
  height: 26px;
  width: 26px;
  border-radius: 50%;
  transition: .4s;
}

.help {
  @apply dvs-rounded dvs-p-8 dvs-bg-grey-lightest dvs-text-sm dvs-border-l-4 dvs-border-blue;
}

.help.danger {
  @apply dvs-bg-red dvs-text-white;
}

.help p {
  @apply dvs-text-blue;
}

.dvs-collapsable .dvs-switch-sm,
.dvs-collapsable .dvs-switch {
  @apply dvs-rounded-sm dvs-bg-grey-light dvs-font-bold dvs-p-4 dvs-cursor-pointer dvs-opacity-75;
}

.dvs-collapsable .dvs-switch-sm {
