  transition: 0.3s width ease;
}

.alert-message {
  @apply dvs-fixed dvs-pin-b dvs-pin-l dvs-ml-8 dvs-z-50 dvs-rounded dvs-bg-blue-dark dvs-text-white dvs-shadow-lg dvs-p-4;
}

.alert-message h5 {
