.fade-enter-active {
  transition: opacity .5s;
  transition-delay: .25s;
}

.fade-leave-active {
  transition: opacity .5s;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}

.fade-enter-to {
  opacity: 1;
}

h1, h2, h3, h4, h5, h6 {
