trix-toolbar .trix-dialogs {
  position: relative;
}

trix-toolbar .trix-dialog {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  font-size: 0.75em;
  padding: 15px 10px;
  background: #fff;
  box-shadow: 0 0.3em 1em #ccc;
  border-top: 2px solid #888;
  border-radius: 5px;
  z-index: 5;
}

trix-toolbar .trix-input--dialog {
  font-size: inherit;
  font-weight: normal;
  padding: 0.5em 0.8em;
  margin: 0 10px 0 0;
  border-radius: 3px;
  border: 1px solid #bbb;
  background-color: #fff;
  box-shadow: none;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

trix-toolbar .trix-input--dialog.validate:invalid {
  box-shadow: #F00 0px 0px 1.5px 1px;
}

trix-toolbar .trix-button--dialog {
  font-size: inherit;
  padding: 0.5em;
  border-bottom: none;
}

trix-toolbar .trix-dialog--link {
  max-width: 600px;
}

trix-toolbar .trix-dialog__link-fields {
  display: flex;
  align-items: baseline;
}

trix-toolbar .trix-dialog__link-fields .trix-input {
  flex: 1;
}

trix-toolbar .trix-dialog__link-fields .trix-button-group {
  flex: 0 0 content;
  margin: 0;
}

trix-editor [data-trix-mutable=true] {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

trix-editor [data-trix-mutable=true] img {
  box-shadow: 0 0 0 2px highlight;
}

trix-editor [data-trix-mutable=true].attachment.attachment--file {
  box-shadow: 0 0 0 2px highlight;
  border-color: transparent;
}

trix-editor [data-trix-mutable=true]::-moz-selection,
trix-editor [data-trix-cursor-target]::-moz-selection {
  background: none;
}

trix-editor [data-trix-mutable=true]::selection,
trix-editor [data-trix-cursor-target]::selection {
  background: none;
}

trix-editor .attachment:hover {
  cursor: default;
}

trix-editor .attachment--preview .attachment__caption:hover {
  cursor: text;
}

trix-editor .attachment__progress {
  position: absolute;
  z-index: 1;
  height: 20px;
  top: calc(50% - 10px);
  left: 5%;
  width: 90%;
  opacity: 0.9;
  transition: opacity 200ms ease-in;
}

trix-editor .attachment__progress[value="100"] {
  opacity: 0;
}

trix-editor .attachment__caption-editor {
  display: inline-block;
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  color: inherit;
  text-align: center;
  vertical-align: top;
  border: none;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

trix-editor .attachment__remove {
  cursor: pointer;
}

trix-editor .attachment__remove--icon {
  text-indent: -9999px;
  display: block;
  position: absolute;
  z-index: 1;
  padding: 0;
  margin: 0;
  top: -1.1em;
  left: calc(50% - 0.8em);
  width: 1.8em;
  height: 1.8em;
  line-height: 1.8em;
  border-radius: 50%;
  text-indent: -9999px;
  background-color: #fff;
  border: 2px solid highlight;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.25);
}

trix-editor .attachment__remove--icon::before {
  display: inline-block;
  position: absolute;
  top: 0.1em;
  right: 0.1em;
  bottom: 0.1em;
  left: 0.1em;
  opacity: 0.75;
  content: "";
  background-image: url(data:image/svg+xml,%3Csvg%20height%3D%2224%22%20width%3D%2224%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M19%206.4L17.6%205%2012%2010.6%206.4%205%205%206.4l5.6%205.6L5%2017.6%206.4%2019l5.6-5.6%205.6%205.6%201.4-1.4-5.6-5.6z%22%2F%3E%3Cpath%20d%3D%22M0%200h24v24H0z%22%20fill%3D%22none%22%2F%3E%3C%2Fsvg%3E);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

trix-editor .attachment__remove--icon:hover {
  border-color: #333;
}

trix-editor .attachment__remove--icon:hover::before {
  opacity: 1;
}

.trix-content {
  line-height: 1.5;
}

.trix-content * {
  box-sizing: border-box;
}

.trix-content h1 {
  font-size: 1.2em;
  line-height: 1.2;
  margin: 0;
}

.trix-content blockquote {
  margin: 0 0 0 0.3em;
  padding: 0 0 0 0.6em;
  border-left: 0.3em solid #ccc;
}

.trix-content pre {
  display: inline-block;
  width: 100%;
  vertical-align: top;
  font-family: monospace;
  font-size: 0.9em;
  margin: 0;
  padding: 0.5em;
  white-space: pre;
  background-color: #eee;
  overflow-x: auto;
}

.trix-content ul, .trix-content ol, .trix-content li {
  margin: 0;
  padding: 0;
}

.trix-content ul li, .trix-content ol li, .trix-content li li {
  margin-left: 1em;
}

.trix-content img {
  max-width: 100%;
  height: auto;
}

.trix-content a[data-trix-attachment] {
  color: inherit;
  text-decoration: none;
}

.trix-content a[data-trix-attachment]:hover, .trix-content a[data-trix-attachment]:visited:hover {
  color: inherit;
}

.trix-content .attachment {
  display: inline-block;
  position: relative;
  max-width: 100%;
  margin: 0;
  padding: 0;
}

.trix-content .attachment__caption {
  padding: 0;
  text-align: center;
}

.trix-content .attachment__caption .attachment__name + .attachment__size::before {
  content: ' · ';
}

.trix-content .attachment--preview {
  width: 100%;
  text-align: center;
}

.trix-content .attachment--preview .attachment__caption {
  color: #666;
  font-size: 0.9em;
  line-height: 1.2;
}

.trix-content .attachment--file {
  color: #333;
  line-height: 1;
  margin: 0 2px 2px 0;
  padding: 0.4em 1em;
  border: 1px solid #bbb;
  border-radius: 5px;
}

trix-toolbar {
  @apply dvs-bg-white dvs-rounded-t dvs-border-b-0 dvs-border-grey-dark;
}

trix-toolbar .trix-button-group {
  @apply dvs-border-0 dvs-mb-0;
}

trix-toolbar .trix-button-group button {
  @apply dvs-border-0 dvs-py-6;
}

trix-toolbar .trix-button-group button:not(:first-child) {
  @apply dvs-border-0;
}

trix-toolbar .trix-button-group button::before {
  @apply dvs-border-0;
  background-image: none !important;
  font-family: 'FontAwesome';
  font-size: 12px;
  line-height: 3rem;
  text-indent: 0;
}

trix-toolbar .trix-button--icon:disabled::before {
  opacity: 1;
}

trix-toolbar .trix-button--icon-bold:before {
  content: '\f032';
}

trix-toolbar .trix-button--icon-italic::before {
  content: '\f033';
}

trix-toolbar .trix-button--icon-link::before {
  content: '\f0c1';
}

trix-toolbar .trix-button--icon-strike::before {
  content: '\f0cc';
}

trix-toolbar .trix-button--icon-quote::before {
  content: '\f10e';
}

trix-toolbar .trix-button--icon-code::before {
  content: '\f121';
}

trix-toolbar .trix-button--icon-bullet-list::before {
  content: '\f03a';
}

trix-toolbar .trix-button--icon-number-list::before {
  content: '\f0cb';
}

trix-toolbar .trix-button--icon-decrease-nesting-level::before {
  content: '\f03b';
}

trix-toolbar .trix-button--icon-increase-nesting-level::before {
  content: '\f03c';
}

trix-toolbar .trix-button--icon-undo::before {
  content: '\f0e2';
}

trix-toolbar .trix-button--icon-redo::before {
  content: '\f01e';
}

trix-toolbar .trix-button--icon-heading-1::before {
  content: '\f1dc';
}

trix-editor {
  @apply dvs-rounded-none dvs-rounded-b dvs-border-grey-dark dvs-text-grey-dark dvs-p-4;
}

@tailwind utilities;



